<template>
    <v-container :fluid="true">
      <div class="text-center">
          <h2>Fecha de Registro Laboratorio</h2>
          <h3>Fecha: {{FechaActual()}}</h3>
      </div>
        <v-form @submit.prevent="Guardar"   ref="formularioLaboratorio"   lazy-validation  >
      <v-row >
        <v-col cols="12" sm="6" md="4" >
          <v-row no-gutters>
           <v-col cols="10" >
              <v-text-field :autofocus="true" :rules="dniRules" v-model="dni" type="number" color="red darken-4"  label="DNI" dense outlined background-color="red lighten-4" ></v-text-field>
           </v-col>
           <v-col cols="2" :class="{'disable-events':btnObtenerPaciente}">
             <v-btn :disabled="dni.length!==8 " class="py-1" block dark color="red darken-4" @click="obtenerPacienteLabClinico">
                <v-icon>
                  search
                </v-icon>
             </v-btn>
           </v-col>
          </v-row>
          <v-text-field :rules="requeridoRules" v-model="nombres"  color="red darken-4" label="Nombres" dense outlined background-color="red lighten-4" ></v-text-field>
          <v-text-field :rules="requeridoRules" v-model="apellidos"  color="red darken-4" label="Apellidos" dense outlined background-color="red lighten-4" ></v-text-field>
        </v-col>
         <v-col cols="12" sm="6" md="4" >
          <!-- El item-text es el valor del nombre del objeto con el que se buscara en el array object, ES decir si pongo group o apellido buscara por grupo o apellido pero no recnocera nombre
           sino por el valor que ponga ahi
          item-value es el valor que ingresara al array del v-model si es que pongo que sea multiple -->
          <v-text-field :rules="requeridoRules"  v-model="fechaNacimiento" type="date" color="red darken-4" label="Fecha Nacimiento" dense  outlined background-color="red lighten-4"  ></v-text-field>
          <v-text-field :rules="telefonoRules" v-model="telefono" type="number"  color="red darken-4" label="Teléfono" dense  outlined background-color="red lighten-4"  ></v-text-field>
         <v-select v-model="empresa" :items="itemsEmpresa" item-text="descripcion" item-value="descripcion" item-color="red darken-4"  class="red--text text-lighten-4" label="Empresa" color="red darken-4" background-color="red lighten-4"  dense  outlined ></v-select>
          <v-text-field :rules="empresaRules" v-if="empresa==='OTROS'" :autofocus="true"  v-model="nuevaEmpresa" type="text" color="red darken-4"  label="Nueva Empresa" dense  background-color="red lighten-4" ></v-text-field>         
        </v-col>
                                                                                                                                     
         <v-col cols="12" sm="6" md="4" >
          <v-select :items="dataTipoMuestraLab" 
          item-color="red darken-4" 
           class="red--text text-lighten-4" 
          label="Tipo de Muestra" 
          color="red darken-4" 
          background-color="red lighten-4"  
          item-text="descripcion"
          item-value="idTipoMuestraLab"
          v-model="dataTipoMuestraLabSelected"
          dense  outlined ></v-select>
         <div class="d-flex flex-row "  v-if="dataMuestraLab.length>0" >
            <v-autocomplete
                  dense
                  v-model="examen"  
                  :items="dataMuestraLab"
                  outlined
                  chips
                  deletable-chips
                  color="red darken-4"
                  label="descripcion"
                  item-text="descripcion"
                  item-value="idMuestraLab"
                background-color="red lighten-4"  >
                  <!-- <template v-slot:item="data">
                      <v-list-item-content active-class="green--text" :disabled="true" >
                        <v-list-item-title @click="saludar()" class="red--text text-lighten-4" v-html="data.item.muestra"></v-list-item-title>
                      </v-list-item-content> 
                  </template> -->
                </v-autocomplete>
            <v-btn :disabled="examen==='' ?true:false" icon color="white" class="red darken-4 flex-row mx-2" @click="agregarExamen(examen)" ><v-icon>add</v-icon></v-btn>
         </div>         
        </v-col>
   </v-row>
   <h5>Examenes Realizados</h5>
   <p class="font-weight-bold red--text">{{mensajeExamen}} </p>
            <v-row class="ma-0 pa-0" >              
              <v-col cols="12" sm="6" md="4 mb-2" class="ma-0 pa-0" v-for="(item,index) in examenesLab" :key ="index" >
                <v-card  outlined  class="pt-3 mx-1 px-1 red lighten-3" >
                  <!-- {{item.id}} -->
                  <v-btn @click="eliminarExamen(item.id)" class="float-right red" :x-small="true" color="white"  icon> <v-icon>close</v-icon></v-btn>
                  <v-row class="ma-0 pa-0">
                <v-col class="ma-0 pa-0" cols="6">
                <v-switch  dense  v-model="examenesRealizadosLab" :label="item.muestra" :value="item.id" class="ma-0 pa-0" color="red darken-4" >  </v-switch>
              </v-col>
               </v-row>   
               
                        <v-row  v-if="verificarExamenRealizado(item.id,index)"  class="ma-0 pa-0">
                        <v-col  cols="10" class="ma-0 pa-0">
                          <v-text-field :rules="requeridoRules" v-model="examenesLab[index].nroVoucher"  color="red darken-4" label="Boleta o Factura" dense outlined background-color="red lighten-4" id="nroVoucher" ></v-text-field>
                        </v-col>
                        <v-col class="ma-0 pa-0" cols="1">
                          <v-btn   @click="copiarTexto()"   block dark color="red darken-4" > <v-icon>content_copy</v-icon></v-btn>
                        </v-col>
                       </v-row>       
                          <v-row v-if="verificarExamenRealizado(item.id,index)" justify="center" class="ma-0 pa-0">
                            <v-col  cols="6" class="ma-0 pa-0">
                              <v-checkbox
                                class="ma-0 pa-0"  
                              dense
                              v-model="examenesLab[index].enviado"
                              label="¿Enviado?"
                              color="red darken-4"
                              :value="!enviado"
                              hide-details
                            ></v-checkbox>
                            </v-col>
                            <v-col  cols="6" class="ma-0 pa-0">
                                  <v-text-field v-model="examenesLab[index].fechaResultado" type="date"  color="red darken-4" label="Entrega del Resultado" dense  outlined background-color="red lighten-4"  ></v-text-field>
                            </v-col>
                            </v-row>   
                </v-card>
              </v-col>
            </v-row>
  <v-row justify="center" class="ma-0 pa-0">
      <v-col cols="6" v-if="ambienteDeModificacion==false">
     <v-btn  class="px-5" :class="{'disable-events':btnGuardarDeshabilitado}" block dark color="red darken-4" type="submit">Guardar</v-btn>
    </v-col>
     <v-col v-if="ambienteDeModificacion==true" cols="6">
       <v-row >
         <v-col cols="6"> 
           <v-btn class="px-5 mx-1" :class="{'disable-events':btnGuardarDeshabilitado}" block dark color="red darken-4" @click="Modificar()" >Modificar</v-btn>
        </v-col>
         <v-col cols="6">
            <v-btn class="px-5 mx-1 " block dark color="darken-4" @click="Cancelar()" >Cancelar</v-btn>
         </v-col>
        </v-row>
    </v-col>
  </v-row>
  <v-row justify="center" class="ma-0 pa-0">
    <v-alert v-if="mensajeResultadoTransaccion!==''" dense outlined :type="tipoMensajeResultado? 'success': 'error'">
          {{ mensajeResultadoTransaccion }}</v-alert>
 
  </v-row>
  </v-form>
  <v-row>
     <v-col cols="11"  class="mx-auto">
       <Tabla @obtenerFechaHoy="FechaActual" @fillDataModificarLab="fillDataToMod" :fechaActual="mensaje" ref="tabla" ></Tabla> 
      </v-col>
  </v-row>
   <v-dialog v-model="dialogNoData" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
           {{mensajeNoData}}
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout :justify-center="true">
           
            <v-btn @click="dialogNoData=false" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
    </v-container>
</template>
<script>
import  {mapState,mapActions} from 'vuex'
import Tabla from '../components/TablaLaboratorio'
export default {
    name:"LabClinico",
    components:{Tabla},
    data() {
        return {
          btnGuardarDeshabilitado:false,
          btnObtenerPaciente:false,
          dialogNoData:false,
          mensajeNoData:'',
          mensajeResultadoTransaccion:'',
          tipoMensajeResultado:false,
          nuevaTipoMuestra:false,
          nuevaMuestra:'',
          ambienteDeModificacion:false, 
          dniRules: [(v) => (!!v && (v.length==8) )|| "8 Numeros"],
          requeridoRules: [(v) => !!v || "es requerido"],
          empresaRules:[(v) => !!v || "es requerido"],
          telefonoRules:[(v) => (!!v && v.length>8 && v.length<12)|| "cantidad min 9 y 11 max"],
          examen:'',        
          //contenedor de temporal de datos que seran eliminados
          dataTempToDeleteAboutModify:[],
          //esta variable contiene a todos los que estana ctivos o examenes realizados switch True 
          examenesRealizadosLab:[],
          enviado:true,
          dataTipoMuestraLab:[],
          dataTipoMuestraLabSelected:0,
          dataMuestraLab:[],
          itemsEmpresa:[],
          //esta variable contiene TODO LOS DATOS de los examenes que seran registrados omodificados
            examenesLab:[],
            dni:'',
            nombres:'',
            apellidos:'',
            fechaNacimiento:'',
            telefono:'',
            empresa:'',
            nuevaEmpresa:''
      }
    },
    mounted(){
        this.getTipoMuestraLab();
      this.listarEmpresa();
    },
    computed:{
      mensajeExamen:function(){
        if(!this.examenesLab.length>0)
            return 'Se require seleccionar Examenes'
            return ''
        },
        ...mapState(['token'])
    },
    watch:{
          dataTipoMuestraLabSelected:function(val){
              if(val>0) this.getMuestraLab(val)
          }},
    methods: {
        ...mapActions(['getPaciente']),
        async obtenerPacienteLabClinico(){
           this.btnObtenerPaciente= await true;
           this.nombres='';
           this.apellidos='';  
           const api =localStorage.getItem('api');
           var dataPaciente= await this.getPaciente({payload:this.dni});

          if(dataPaciente.nombres){
            this.nombres=dataPaciente.nombres;
            this.apellidos=dataPaciente.apellidos;
          }else{
               if(dataPaciente=="Ha superado la cantidad de consultas mensuales: 250" && api!=2){
                  localStorage.setItem('api',2);
                  this.obtenerPacienteLabClinico();
                  }else{
                     this.mensajeNoData=dataPaciente;
                      this.dialogNoData=true;
                   }
              }
            this.btnObtenerPaciente=await false;
          },
       listarEmpresa(){
        this.axios.get('/listarEmpresa')
        .then(res=>{
          
          res.data.push({idEmpresa:0,descripcion:'OTROS'});
          this.itemsEmpresa=res.data;
        })
        .catch(err=> console.log(err))
      },
        getTipoMuestraLab(){
          this.axios.get('/listarTipoMuestraLab')
          .then(res=>{this.dataTipoMuestraLab=res.data; })
          .catch(err=>{ console.log(err); })
        },
       getMuestraLab(e){
          this.axios.get(`/listarMuestraLab/${e}`)
          .then(res=>{  this.dataMuestraLab=res.data;})
          .catch(err=>{   console.log(err);  })
          },
      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
          this.mensaje=año+'-'+mes+'-'+dia;
          return año+'-'+mes+'-'+dia;
      
      },
      convertidorFecha(str){
          if(str==null){
            return 'Sin Data'
          }else{
            const f = new Date(str);
              var año=f.getFullYear()
              var mes=(f.getMonth() + 1)
                  mes=(mes<10)?('0'+mes):mes;
              var dia=f.getDate()
                  dia=(dia<10)?('0'+dia):dia;
          return año+'-'+mes+'-'+dia;
          }
     } ,
     Guardar(){
   
         var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
        var existeExamen=this.examenesLab.length>0;
        var regLaboratorio = this.$refs.formularioLaboratorio.validate();

        if(existeExamen && regLaboratorio ){
              this.btnGuardarDeshabilitado=true;
          this.axios.post("/saveExamLaboratorio",{
                  dni:this.dni,
                  nombres:this.nombres.trim(),
                  apellidos:this.apellidos.trim(),
                  fechaNacimiento:this.fechaNacimiento,
                  telefono:this.telefono,
                  empresa:this.nuevaEmpresa===''? this.empresa:this.nuevaEmpresa,
                  fechaRegistroExamen:this.FechaActual(),
                  examenes:this.examenesLab
          },{headers}) 
          .then(res=>{
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesLab=[]
                this.examenesRealizadosLab=[]

                if(res.data.mensaje){
                this.tipoMensajeResultado=true;
                this.mensajeResultadoTransaccion=res.data.mensaje;           
                setTimeout(()=>{
                  this.mensajeResultadoTransaccion=''
                   this.tipoMensajeResultado=false},2000 );}

                this.$refs.tabla.ListarMuestras();
                this.btnGuardarDeshabilitado=false;
                
          })
          .catch(err=>{ 
                this.$refs.tabla.ListarMuestras();
                err.response.data.sqlMessage? this.mensajeResultadoTransaccion=err.response.data.sqlMessage :   this.mensajeResultadoTransaccion=err;            
                setTimeout(()=>{this.mensajeResultadoTransaccion=''},2000 )
                 this.btnGuardarDeshabilitado=false;
                 })
            }   
      },
      verificarExamenRealizado(ele,i){
        
            var existe=this.examenesRealizadosLab.includes(ele);
            if(!existe){
                   this.examenesLab[i].atendido=false
                   this.examenesLab[i].enviado=false
                   this.examenesLab[i].fechaResultado=''}
                  else{
                      this.examenesLab[i].atendido=true
                   }

      return existe;
      },
      agregarExamen(e){
        if(e!==null){
          //este dato sera agregado a la lista de los card examenes
            var examsLab={};
            if(this.ambienteDeModificacion){
                    this.dataMuestraLab.filter(ele=>{if(ele.idMuestraLab===e) examsLab={
                    doExamen:'Nuevo',
                    idPaciente:this.examenesLab[0].idPaciente,
                    idExamen:0,id:ele.idMuestraLab,muestra:ele.descripcion,atendido:false,enviado:false,fechaResultado:''} });
                }else{
                  this.dataMuestraLab.filter(ele=>{if(ele.idMuestraLab===e) examsLab={id:ele.idMuestraLab,muestra:ele.descripcion,atendido:false,enviado:false,fechaResultado:''} });
                }         
                var existe=false;
                this.examenesLab.filter(x=>{ if(x.id===examsLab.id) existe=true })  
            
                  if(!existe)
                    this.examenesLab.push(examsLab);
                  this.examen='' 
                  
        }
      },
      eliminarExamen(e){  
       if(this.ambienteDeModificacion){
          this.examenesLab= this.examenesLab.filter(elem=>{
           if(elem.id==e && elem.doExamen=='Modificar')  { 
             elem.doExamen="Eliminar"
             //agregando al contenedor de eliminacion
             this.dataTempToDeleteAboutModify.push(elem);
              }
              return elem;
              })
       }
          this.examenesLab= this.examenesLab.filter(elem=>{ if(elem.id!==e) {return elem}})
          this.examenesRealizadosLab=this.examenesRealizadosLab.filter(ele=>{if(ele!==e) {return ele}})

      },
      //llenar datos en el formulario para editar
      fillDataToMod(d){
        this.dataTempToDeleteAboutModify=[]
        this.ambienteDeModificacion=true;
      //Vaciamos par modificar otro dato
          this.examenesLab=[];
          this.examenesRealizadosLab=[];
              this.dni=d.dni
                this.nombres=d.nombres
                this.apellidos=d.apellidos
                this.fechaNacimiento=this.convertidorFecha(d.fechNac)
                this.telefono=d.telefono
                this.empresa=d.empresa
                var iterador=0;
               for (let index = 0; index < d.detalleExamen.length; index++) {         
                   if (!(d.detalleExamen[index].fechaAtencion==null)){
                   //agregando a los examenes realizados si es que tien fecha de atendido
                      this.examenesRealizadosLab[iterador]=d.detalleExamen[index].idMuestraLab
                      iterador++;   }  
                  this.examenesLab.push({
                  doExamen:'Modificar',
                  idPaciente:d.detalleExamen[index].idPaciente,
                  idExamen:d.detalleExamen[index].idExamen,
                  nroVoucher:d.detalleExamen[index].nroVoucher,
                  id:d.detalleExamen[index].idMuestraLab,
                  muestra:d.detalleExamen[index].examen,
                  atendido:d.detalleExamen[index].fechaAtencion==null? false:true ,
                  enviado:d.detalleExamen[index].fechaEnvioMuestra==null? false:true ,
                  fechaResultado:d.detalleExamen[index].fechaEntregaResultado==null? '':this.convertidorFecha(d.detalleExamen[index].fechaEntregaResultado)})
                  
               } 
               iterador=0;
      },
      Modificar(){
           var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
       var eData=this.examenesLab;
       if(this.dataTempToDeleteAboutModify.length>0){
             eData=[...eData,...this.dataTempToDeleteAboutModify]}
              if( eData.length==0){
                  eData=this.dataTempToDeleteAboutModify;}
        var existeExamen=eData.length>0;
        var regLaboratorio = this.$refs.formularioLaboratorio.validate();

        if(existeExamen && regLaboratorio ){
          this.btnGuardarDeshabilitado=true;
          this.axios.post("/modificarExamLaboratorio",{
                  dni:this.dni,
                  nombres:this.nombres.trim(),
                  apellidos:this.apellidos.trim(),
                  fechaNacimiento:this.fechaNacimiento,
                  telefono:this.telefono,
                  empresa:this.nuevaEmpresa===''? this.empresa:this.nuevaEmpresa,
                  fechaRegistroExamen:this.FechaActual(),
                  examenes:eData
          },{headers})
         .then(res=> {
                this.$refs.tabla.ListarMuestras();
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesLab=[]
                this.examenesRealizadosLab=[]
                 this.dataTempToDeleteAboutModify=[]
                 this.ambienteDeModificacion=false;
                 
                 if(res.data.mensaje){
                this.tipoMensajeResultado=true;
                this.mensajeResultadoTransaccion=res.data.mensaje;           
                setTimeout(()=>{
                  this.mensajeResultadoTransaccion=''
                   this.tipoMensajeResultado=false},2000 );}

               this.btnGuardarDeshabilitado=false;


          })
          .catch(err=>{ 
              this.$refs.tabla.ListarMuestras();
             err.response.data.sqlMessage?this.mensajeResultadoTransaccion=err.response.data.sqlMessage :   this.mensajeResultadoTransaccion=err;            
             setTimeout(()=>{this.mensajeResultadoTransaccion=''},3000 )
             this.btnGuardarDeshabilitado=false;
                })  
        }
      },
      Cancelar(){
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesLab=[]
                this.examenesRealizadosLab=[]
                 this.dataTempToDeleteAboutModify=[]
                 this.ambienteDeModificacion=false;
      },
    copiarTexto(){
       let input=document.getElementById("nroVoucher");
       /*obtiene el elemento que esta seleccionado*/
       const selected =  document.getSelection().rangeCount > 0  ? document.getSelection().getRangeAt(0) : false;  
          input.select();
         document.execCommand("copy"); 
          /*Si estas seleccionado entonces no sombreará*/
          if(selected){
          document.getSelection().removeAllRanges();    
          document.getSelection().addRange(selected);
          }
    }
    },
}
</script>
<style scoped>
.disable-events {
  pointer-events: none
}
.v-input__slot{
margin:0;
}
.v-input__control.v-messages.theme--light.red--text .text--darken-4{
  display: none !important;
}
.theme--light.v-application{
  background-color:#FFCDD2;
}

.theme--light.v-list{
  background: #FFCDD2;
}

::v-deep input::-webkit-outer-spin-button,
::v-deep input::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}

</style>