<template>
  <div>
      <v-row class="ma-0">
    <v-col cols="12" md="5" class=" pa-1">
       <v-text-field type="date" v-model="desde" color="red darken-4" label="Desde" dense outlined background-color="red lighten-4" ></v-text-field>
    </v-col>
    <v-col cols=10 md="5" class=" pa-1">
       <v-text-field type="date" v-model="hasta" color="red darken-4" label="Hasta" dense outlined background-color="red lighten-4" ></v-text-field>
    </v-col>
    <v-col cols="2" md="2" class="ma-0  pa-1">
       <v-btn  class="py-1" block dark color="red darken-4" @click="ListarMuestras">
                <v-icon>
                  search
                </v-icon>
             </v-btn>
      </v-col>
  </v-row>

    <v-simple-table dark dense fixed-header class="my-2" max-height="10vh">
      <template v-slot:default>
        <thead>
          <tr>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
              style="
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
              "
            >
              *
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Empresa
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Dni
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Paciente
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Examen(Cant.)
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              FechNac
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            >
              Telefono
            </th>
            <th
              class="red darken-4 text-center subtitle-1 font-weight-bold white--text"
            ></th>
          </tr>
        </thead>
        <tbody>
          <tr  v-for="(item, index) in LaboratorioData" :key="index">
            <td class="text-center">{{ index +1}}</td>
            <td class="text-center">{{ item.empresa }}</td>
            <td class="text-center">{{ item.dni }}</td>
            <td class="text-center">{{ item.nombres }} {{item.apellidos}}</td>
            <td class="text-center">{{ item.examenes}}</td>
            <td class="text-center">{{ convertidorFecha(item.fechNac) }}</td>
            <td class="text-center">{{ item.telefono }}</td>
            <td class="text-center">
              <v-btn @click="toDataDialog(item.detalleExamen,item.nombres +' '+ item.apellidos)" elevation="3" icon outlined small class="ma-1">        
                <v-icon>view_list</v-icon>
              </v-btn>
              <v-btn  @click="editarDataPat(item)"  elevation="3" icon outlined small class="ma-1">        
                <v-icon>edit</v-icon>
              </v-btn>
              <v-btn
                elevation="3"
                icon
                outlined
                small
                class="ma-1"
                @click="ShowDialogDeletePat(item.examenes, item.nombres +' '+ item.apellidos,item.id )" >
                <v-icon>delete</v-icon>
              </v-btn>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <v-dialog v-model="dialogDeleteExamPat" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ dataDialogDeletePat.message }}
            <span class="text-center red--text text-darken-4 ma-2">
              {{ dataDialogDeletePat.examen }}
            </span>
          </div>
          <div class="font-weight-bold">
            de
            <span class="text-center red--text text-darken-4 ma-2">
              {{ dataDialogDeletePat.nombre }}
            </span>
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-space-between="true">
            <v-btn
              @click="Eliminar(dataDialogDeletePat.id)"
              rounded
              dark
              color="red darken-4"
              >Si</v-btn
            >
            <v-btn @click="NotShowDialogDeletePat" rounded dark>No</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
     <v-dialog v-model="dialogDetalleExamPat"   max-width="350" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ dataDialogDetallePat.nombre }}
          </div>
        </v-list-item-content>
        <v-simple-table dark dense fixed-header >
          <template v-slot:default>
            <thead >
                <tr>
                  <th>
                    nroVoucher
                  </th>
                <th>
                  Examen
                </th>
                  <th>
                    Registrado
                  </th>
                  <th>
                    Atendido
                  </th>
                  <th>
                    Resultado
                  </th>
                </tr>
            </thead>
              <tbody>
                  <tr v-for="(item,index) in dataDialogDetallePat.data " :key="index">
                     <td  :class="{ 'red':item.nroVoucher==null}" class=" text-center">{{ item.nroVoucher }}</td>
                    <td class=" text-center">{{ item.examen}}</td>
                     <td  :class="{ 'red':item.fechaRegistroExamen==null}" class=" text-center">{{  convertidorFecha (item.fechaRegistroExamen) }}</td>
                     <td :class="{ 'red':item.fechaAtencion==null}" class="text-center">{{ convertidorFecha(item.fechaAtencion )}}</td>
                     <td :class="{ 'red':item.fechaEntregaResultado==null}"    class="text-center">{{ convertidorFecha(item.fechaEntregaResultado )}}</td>
                  </tr>
              </tbody>
          </template>
        </v-simple-table>
        <v-list-item-content>
          <v-layout mx-2 :justify-center="true">
            <v-btn @click="dialogDetalleExamPat=false" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
  name: "TablaPatologia",
  data() {
    return {
      dialogDetalleExamPat:false,
      dialogDeleteExamPat: false,
       dataDialogDeletePat: {
        message: "Eliminar",
        id: null,
        examen: null,
        nombre: null,
      },
      dataDialogDetallePat: {
        nombre: "",
        data:[]
      },
      LaboratorioData: [],
      dataDetalleExam: [],
        desde:'',
       hasta:''
    };
  },  
  computed:{
    ...mapState(['token'])
  },
 async mounted(){
   await this.inicializar();
    this.ListarMuestras();
  },
  methods: {
     inicializar(){
        this.desde=this.FechaActual(); 
        this.hasta=this.FechaActual(); 
      },
        FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
    toDataDialog(data,nombre){
      this.dataDialogDetallePat.data=data;
       this.dataDialogDetallePat.nombre=nombre;
      this.dialogDetalleExamPat=true;
    },
    convertidorFecha(str){
      if(str==null){
        return 'Sin Data'
      }else{
        const f = new Date(str);
          var año=f.getFullYear()
          var mes=(f.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=f.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return dia+'/'+mes+'/'+año;
      }
    },
    ShowDialogDeletePat(exam, name, id) {
      this.dataDialogDeletePat.examen = exam;
      this.dataDialogDeletePat.nombre = name;
      this.dataDialogDeletePat.id = id;
      this.dialogDeleteExamPat = true;
    },
    NotShowDialogDeletePat() {
      this.dialogDeleteExamPat = false;
      this.dataDialogDeletePat.data = null;
      this.dataDialogDeletePat.id = null;
    },
    Eliminar(id) {
      console.log("Eliminando.." + id);
      this.dialogLaboratorio = false;
    },
    editarDataPat(data){
      //console.log(data)
  
      this.$emit('fillDataModificarLab',data);
    },
    ListarMuestras() {
        var headers = {
            "Content-Type": "application/json",
            token: this.token,
          };

       this.LaboratorioData=[];
       this.dataDetalleExam= [];
       this.dataDialogDetallePat= {
        nombre: "",
        data:[]
        };
      this.axios
        .post("/listarExamenLaboratorio",{desde:this.desde,hasta:this.hasta},{headers})
        .then((res) => {
     
          var examen = "";
          var dni="";
          //variable que contendra el dato de los examenes en el array para mostrar en  el dialog 
          var contTempDataDialog=[];
          var dataTemp = res.data;
          for (let i = 0; i < res.data.length; i++) {
             dni= dataTemp[0].dni;
             //primero guardamos los datos que no son igual a dni A
            var dataTempNoValido = dataTemp.filter((e) => {
              if (e.dni != dni) return e;
            });
            //sacamos los datos con dni A
            dataTemp = dataTemp.filter((e) => {
              if (e.dni == dni) return e;
            });
            //concatenamos los datos de los examenes para imprimirlos en su solo dato
            for (let j = 0; j < dataTemp.length; j++){
              //guardamos los datos de la persona con DNI A para mostrarlo en un dialog despues
                contTempDataDialog.push(dataTemp[j])
              if (j == 0) {
                examen = dataTemp[j].examen;
                continue;
              }
              examen = examen+", " + dataTemp[j].examen;
            }
            this.dataDetalleExam.push({id:dataTemp[0].idPaciente,idExamen:dataTemp[0].idExamen,empresa:dataTemp[0].empresa, dni: dni, 
                                       nroVoucher:dataTemp[0].nroVoucher, nombres:dataTemp[0].nombres,apellidos:dataTemp[0].apellidos,
                                       fechNac:dataTemp[0].fechaNacimiento,telefono:dataTemp[0].telefono,
                                        examenes: examen,detalleExamen:contTempDataDialog,idMuestraPat:dataTemp[0].idMuestraLab});
            dataTemp = dataTempNoValido;
            examen = "";
            dni="";
            contTempDataDialog=[];
            if(dataTemp.length==0)
                break;
          }
             this.LaboratorioData=this.dataDetalleExam;
        })
        .catch((err) => {
          console.log(err);
        });
          },
  },
};
</script>